@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hahmlet:wght@300&family=Secular+One&display=swap);
*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0}
* {
    margin: 0;
    padding: 0; 
  }
  
  /* Icon 1 */
  
  #nav-icon1, #nav-icon2, #nav-icon3, #nav-icon4 {
    width: 60px;
    height: 45px;
    position: relative;
    margin: 50px auto;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
  }
  
  #nav-icon1 span, #nav-icon3 span, #nav-icon4 span {
    display: block;
    position: absolute;
    height: 9px;
    width: 100%;
    background: #131313;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }
  
  #nav-icon1 span:nth-child(1) {
    top: 0px;
  }
  
  #nav-icon1 span:nth-child(2) {
    top: 18px;
  }
  
  #nav-icon1 span:nth-child(3) {
    top: 36px;
  }
  
  #nav-icon1.open span:nth-child(1) {
    top: 18px;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
  #nav-icon1.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
  #nav-icon1.open span:nth-child(3) {
    top: 18px;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }
  
  #nav-icon1{
      display: none;
  }
  @media (max-width:700px) {
      #nav-icon1{
          display: block;
      }
  }
body{overflow-x:hidden}.navbar{display:grid;grid-template-columns:1fr 3fr;align-items:center;overflow:hidden;position:fixed;width:100%;padding-left:1em;padding-right:1em;margin-left:-0.02em;margin-top:-0.5em;color:#000;font-family:"Ubuntu"}.navbar>h1{margin-top:.8em}.navbar ul{list-style:none;display:grid;grid-template-columns:repeat(6, 1fr);justify-self:end;margin-top:1em;transition:1s}ul li a{margin-left:2em;height:auto;align-items:center;text-align:center;vertical-align:center;padding:1.5em;padding-top:2.5em;transition:1s;text-decoration:none;color:#000}li a:hover{background-color:brown}@media(max-width: 700px){.navbar h1{display:flex}h1 #nav-icon1{margin-top:-3px;margin-left:7rem;width:60px;height:50px}h1{z-index:1}.navbar{grid-template-columns:1fr;width:100%}.navbar>ul{grid-template-columns:1fr;margin-left:-2rem;background-color:#fff;width:400px;padding:1em;height:100vh;margin-top:0;position:fixed;align-items:center}ul>li:nth-child(1){margin-top:5rem}ul>li{align-self:center;border-bottom:1px solid gray;width:103%;padding-bottom:1.5em;padding-left:4.5rem}ul>li a:hover{background-color:blue}.navigation-close{left:-100%}.navigation-open{left:0%}}
.hero-main{display:grid;grid-template-columns:60% 40%;height:90vh;margin-top:3em}.heading-section{display:flex;justify-content:center;align-items:center;flex-direction:column}body{background-color:#fff}.name{font-size:3em;font-weight:bold;margin-left:-1em;margin-bottom:-0.9em;margin-top:-2em;font-family:"Secular One",sans-serif}.about{margin-left:-4em;font-weight:600;font-family:"Hahmlet",serif;color:#5f5e5e;text-decoration:underline}button{margin:1em;padding:1em;border-radius:1em;border:.5px solid gray;font-family:"Ubuntu";font-weight:600;background-color:#fff;transition:1s}button:nth-child(2){background-color:#000;color:#fff}button:hover{background-color:gray;color:#fff}.buttonWrap{margin-left:-5em}.cursor{font-size:3rem}@media(max-width: 700px){.hero-main{grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));margin-top:5rem}.cursor{font-size:2rem;z-index:-3}.box-object{z-index:-1}.name{font-size:1.5rem;margin-left:.25em;margin-bottom:-4em}.about{font-size:.75rem;margin-left:.25em;margin-top:-1em}.buttonWrap{margin-left:2px}.hero-main{opacity:1}}
